li, button, input, .App, div, span, h1, h2, h3 {
  font-family: 'Barlow', sans-serif;
}
a {
  text-decoration: none;
  color: #fff;
}

@media(hover: hover) {
  a:hover {
    color: #f2a;
  }
}

p {
  font-family: 'Roboto Mono', sans-serif;
  font-size: 1rem;
}

h1 {
  font-family: 'Barlow', sans-serif;
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0 0 40px 0;
}


section p {
  text-align: left;
}

section#about p {
  text-align: center;
}



.App {
  background: #000;
  color: #fff;
  text-align: center;
  min-height: 100vh;
  box-sizing: border-box;
}

.sidebar {
  padding: 20px 0;
  width: 220px;
  min-height: 100vh;
  box-sizing: border-box;
  background: #112;
}

.main {
  background: #224;
  padding: 20px 0;
  min-height: 100vh;
  box-sizing: border-box;
}

.display {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 10px 0;
}

.display .label {
  flex: 0 0 100%;
  font-size: 12px;
}

.display .value {
  margin: auto;
  font-size: 14px;
  padding: 2px 10px;
  background: #446;
  border-radius: 3px;
}

.token-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
}

.token {
  margin: 3px;
  transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

@media(hover: hover) {
  .token:hover {
    transform: scale(1.1);
  }
}

.token img{
  width: 150px;
}



.mint-row {
  display: flex;
  flex-wrap: wrap;
  margin: 15px auto 0 auto;
  /* max-width: 500px; */
}

.mint-row #mint-amount{
  min-width: 70px;
  text-align: center;
}

.mint-bottom-row {
  display: inline-block;
  width: 350px;
  margin: 0 auto;
}

.dropdown {
  display: inline;
}


#mint-amount {
  font-size: 1.4rem;
  font-family: 'Barlow', sans-serif;
  text-align: center;
  display: inline-block;
  height: 60px;
  box-sizing: border-box;
  font-weight: 900;
  outline: none;
  border: none;
  opacity: 1 !important;
  margin: 0 0 0 0px;
  background: #f313a4;
  border-radius: 2px;
  color: #fff !important;
  transition: all 2s cubic-bezier(0.075, 0.82, 0.165, 1);
  color: #fff;
  background-image: linear-gradient(45deg, #f313a4 50%, transparent 50%);
  background-position: 100%;
  background-size: 400%;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.btn:focus {
  background: #fff;
  color: #262022 !important;
  border: 5px solid#fff;
  box-shadow: none;
}
.btn:active {
  background: #f313a4;
  color: #262022 !important;
  border: 5px solid#f313a4;
  box-shadow: none !important;
}


.mint-box {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
  align-items: center;
}
.mint-box .row{
  flex: 0 0 100%;
}

button.btn-primary {
  color: #fff !important;
}

.btn-primary.disabled, .btn-primary:disabled {
  background: #000 !important; 
  color: #666 !important; 
}

.button-container.disabled {
  border-color: #000 !important;
} 

#mint-amount.disabled {
  background: #000 !important;
  opacity: 0.65;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
}


.cost {
  display: flex;
  width: 120px;
  align-items: center;
  justify-content: center;
  background: #f2a;
  border-radius: 2px;
  font-size: 1.4rem;
  padding: 5px 0 0 0;
  height: 40px;
  margin: 0;
}

.mint-data {
  background: #000;
  border-radius: 2px;
  padding: 6px 12px;
  margin: 0px 5px;
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.2);
}

#hero {
  background: #000;
  min-height: 100vh;
  padding: 50px 0 0 0;
}


.hero-minted {
  font-size: 1.5rem;
  margin: 30px 0 8vh 0;
  line-height: 1.2rem;
}


.lead {
  font-size: 1rem;
}

section {
  padding: 80px 0 100px 0;
}

.traits-row {
  margin: 60px 0 0 0;
  background: #fcfcfc;
  padding: 90px 50px;
  border-radius: 2px;
  box-shadow: 0 0 50px rgba(0,0,0,.1);
}

.traits-row h1 {
  color: #111;
}

.traits-row p {
  color: #111;
}

.img-wrapper {
  /* background: #fff; */
  padding: 0;
  margin: 0px;
  border-radius: 14px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0,0,0,.3);
  transition: all .3s cubic-bezier(0.39, 0.575, 0.565, 1);
}



#merch {
  color: #fff;
  background: #262022;
}

#FAQ h1 {
  color: #fff;
}

#accordion-container {
  color: #111;
  max-width: 800px;
  margin: 0 auto;
  background: none;
}



.navbar-toggler {
  border: none !important;
  outline: none !important;
}

.roadmap-description {
  text-align: left;
}

.roadmap-number {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #262022;
  width: 110px;
  text-align: center;
  padding: 10px;
  margin: 0px;
  border-radius: 2px;
