}

.showcase .row {
  --bs-gutter-x: 0;
}

#about {
  color: #fff;
  background: #262022 !important;
  text-align: center;
  box-sizing: border-box;  
}

.section-title svg {
  max-height: 120px;
}

.section-title path {
