}





html, body {
  /* font-family: Barlow, Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif; */
  font-family: "Barlow";
  font-smooth: always !important;
  -webkit-font-smoothing: antialiased !important;
  -moz-osx-font-smoothing: grayscale !important;
  text-rendering: optimizeLegibility !important;
  color: #bbb;
  background: rgb(241, 238, 238);
  max-width: 100vw;
  overflow-x: hidden;
}

#teaser {
  padding: 0;
  font-family: Bebas Neue, Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  margin: 0;
  box-sizing: border-box;
  height: 100vh;
  width: 100vw;
}


/* VIDEO BACKGROUND */
.videoBackground {
  position: relative;
  background-color: black;
  width: 100%;
  padding: 0;
  margin: 0;
  max-width: 100vw;
  box-sizing: border-box;
  overflow: hidden;
}

.videoBackground > video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  -ms-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

.videoBackground .container {
  position: relative;
  z-index: 3;
}

.videoBackground .overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: black;
  opacity: .2;
  z-index: 1;
}

.videoBackground .vignette {
  box-shadow: inset 0 0 100px #000;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  max-width: 100vw;
  max-height: 100vh;
  background-color: none;
  opacity: 1;
  z-index: 2;
}

#video-mobile {
  display: none;
}



.teaser-content {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  align-content: flex-end;
}

.teaser-content div {
  flex: 0 0 100%;
  color: #fff;
  text-align: center;
}

.teaser-content p {
  font-size: 1.4em;
  opacity: 1;
  font-family: Bebas Neue, Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.teaser-content h1 {
  font-size: 5em;
  font-family: Bebas Neue, Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  letter-spacing: .1em;
  text-align: center;
}

.teaser-content h1 span {
  opacity: 1;
}



@media (max-width: 768px) {
  #video-full {
    display: none;
  } 
  #video-mobile {
    display: block;
  } 
  #mint-amount {
    margin-top: 8px;
    margin-bottom: 8px;
  }
  .countdown-number {
    max-width: 65px;
    padding: 5px 0 7px 0;
  }
}

