#navbar-collapse{
  justify-content: space-between;
}

@media (max-width: 768px) {
  .navbar-collapse {
    display: inline-block;
  }  
}

.navbar {
  background: #000 !important;
  padding: 2px 0;
  transition: 0.5s all cubic-bezier(0.075, 0.82, 0.165, 1);
}

.nav-item {
  display: flex;
  align-items: center;
  font-weight: 400;
  text-transform: uppercase;
  padding: 0 0 0 10px;
}

.nav-item svg {
  height: 30px;
  padding: 0;
  margin: 0;
  width: 30px;
}

.nav-item svg path{
  fill: #fff;
  transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

a.nav-link {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 6px 4px 2px 4px;
  color: #fff;
  font-weight: 400;
}

@media(hover: hover) {
  a.nav-link:hover {
    color: #f24;
  }
  .nav-item:hover svg path{
    fill: #f24;
  }
}

button.navbar-toggler:focus {
  outline: none;
  border: none;
  box-shadow: none;
}

a.mint-btn-nav {
  color: #000;
}

.navbar-brand, .navbar-brand:hover {
  color: #f24;
  text-transform: uppercase;
  font-weight: 900;
}

.navbar-toggler svg{
  width: 30px;
}

.navbar-toggler svg path {
  fill: #fff;
}




.mint-btn-nav {
  font-size: 1.2rem;
  padding: 7px 36px 2px 36px;
  font-family: 'Balsamiq Sans', sans-serif;
  font-weight: 900;
  margin: 10px 0 10px 0;
  box-sizing: border-box;
  background: #000;
  color: #fff !important;
  border: none;
  transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.mint-btn-nav:focus {
  border: none;
}


@media(hover: hover) {
  .mint-btn-nav:hover {
    background: #000;
  }
}

.socials svg {
  height: 30px;
  width: 30px;
}

.team-card path {
  fill: #ED9419;
}

.socials {
  float: right;
}

.transparent {
  background: rgba(105, 80, 208, 0) !important;
}
.opaque {
  background: rgba(105, 80, 208, 1) !important;
}
