#rarity {
  color: #fff;
  background: #262022;
}
#rarity p {
  text-align: left;
}

.rarity-special {
  /* max-height: 400px; */
  margin: 0 0 50px 0;
}

#rarity p.rarity-name {
  font-family: 'Barlow', sans-serif;
  margin: 7px auto 10px 0;
  text-align: center;
  font-weight: 900;
  font-size: 1.3rem;
  width: 100%;
}
#rarity img{
  transition: all 0.3s cubic-bezier(0.39, 0.575, 0.565, 1); 
  box-shadow: 0 0 20px rgba(0,0,0,.3);
}

#rarity .row div img {
  border: 10px solid #fff;
}

#rarity .row div:nth-child(1) img:hover {
  border: 10px solid #555;
}
#rarity .row div:nth-child(4) img:hover {
  border: 10px solid #7f0;
}
#rarity .row div:nth-child(2) img:hover {
  border: 10px solid #f24;
}
#rarity .row div:nth-child(3) img:hover {
  border: 10px solid #f0f;
}

#rarity .row div img:hover{
  border: 10px solid #fff;
  transform: scale(1.1) rotate(-2deg);
  box-shadow: 0 0 20px #000;
}

.legendary {
  display: inline-block;
} 
.legendary-mobile {
  display: none;
} 
@media (max-width: 768px) {
  .legendary {
    display: none;
  } 
  .legendary-mobile {
    display: inline-block;
  } 
}

.special-artists {
  display: flex;
  margin-top: 30px;
}
.special-artists a {
  display: inline-block;
  padding: 4px 8px;
  background: #3a3a3a;
  margin: 4px 4px;

}