.button-container {
  position: relative;
  width: 275px;
  height: 61px;
  /* overflow: hidden; */
  border: 4px solid #f24;
  font-family: "Barlow", sans-serif;
  font-weight: 900;
  transition: 0.5s;
  display: inline-block;
  margin: 0 10px 0 0;
  letter-spacing: 1px;
  border-radius: 1px;

}

.button-container button {
  width: 100%;
  height: 100%;
  font-family: "Barlow", sans-serif;
  font-weight: 900;
  border-radius: 0;
  font-size: 1.3rem;
  letter-spacing: 1px;
  font-weight: bold;
  background: #f24;
  color: #fff !important;
  border-color: #000 !important;
  -webkit-mask: url("https://raw.githubusercontent.com/robin-dela/css-mask-animation/master/img/urban-sprite.png");
  mask: url("https://raw.githubusercontent.com/robin-dela/css-mask-animation/master/img/urban-sprite.png");
  -webkit-mask-size: 3000% 100%;
  mask-size: 3000% 100%;
  border: none;
  color: #f24;
  cursor: pointer;
}

.button-container button:hover {
  -webkit-animation: ani 0.7s steps(29) forwards;
  animation: ani 0.7s steps(29) forwards;
}



.mas {
  position: absolute;
  color: #f24 !important;
  text-align: center;
  width: 100%;
  height: 100%;
  font-family: "Barlow", sans-serif;
  font-weight: 900;
  background: #000 !important;
  font-size: 1.3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  overflow: hidden;
  font-weight: bold;
}

@-webkit-keyframes ani {
  from {
    -webkit-mask-position: 0 0;
    mask-position: 0 0;
  }
  to {
    -webkit-mask-position: 100% 0;
    mask-position: 100% 0;
  }
}
@keyframes ani {
  from {
    -webkit-mask-position: 0 0;
    mask-position: 0 0;
  }
  to {
    -webkit-mask-position: 100% 0;
    mask-position: 100% 0;
  }
}
@-webkit-keyframes ani2 {
  from {
    -webkit-mask-position: 100% 0;
    mask-position: 100% 0;
  }
  to {
    -webkit-mask-position: 0 0;
    mask-position: 0 0;
  }
}
@keyframes ani2 {
  from {
    -webkit-mask-position: 100% 0;
    mask-position: 100% 0;
  }
  to {
    -webkit-mask-position: 0 0;
    mask-position: 0 0;
  }
}

