
.team-img {
  width: 100%;
  transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
  transform: scale(.85);
}

#team {
  padding: 70px 0 120px 0;
}

.team-title {
  text-align: center;
  font-size: 2.2em;
  margin: 0 auto 30px auto;
}

.team-name {
  text-align: center;
  margin: auto;
  font-size: 1.5em;
  padding: 4px 10px;
  font-weight: 900;
  text-transform: uppercase;
  transition: all .3s cubic-bezier(0.075, 0.82, 0.165, 1);
}

.team-description {
  text-align: center;
  color: #eee;
  font-size: 1.2em;
}

.team-member:hover .team-img {
  transform: rotateZ(10deg) scale(.95);
}
